.s-rulers {
    padding-top: rem(30);
    overflow-x: visible;

    @media (min-width: 1024px) {
        padding-top: rem(42);
    }

    @media (min-width: 1600px) {
        padding-top: rem(60);
    }

    @media (min-width: 1920px) {
        padding-top: rem(73);
    }

    .l-default {
        position: relative;
        max-width: rem(1520);

        &:after {
            position: absolute;
            bottom: 0;
            left: calc(-100% + 70px);
            width: 100%;
            height: 1px;
            background: $red;
            content: "";

            @media screen and (max-width: 1023px) {
                left: calc(-100% + 25px);
            }

            @media screen and (max-width: 767px) {
                left: calc(-100% + 15px);
            }
        }

        &:before {
            position: absolute;
            right: calc(-100% + 70px);
            bottom: 0;
            width: 100%;
            height: 1px;
            background: $red;
            content: "";

            @media screen and (max-width: 1023px) {
                right: calc(-100% + 25px);
            }

            @media screen and (max-width: 767px) {
                right: calc(-100% + 15px);
            }
        }
    }

    &__tabs {
        position: relative;
        display: flex;
        width: 100%;

        &:after {
            width: 100%;
            min-height: 100%;
            border-bottom: 1px solid $red;
            content: "";
        }
    }

    &__tab {
        padding: 12px 0;
        font-size: rem(13);
        font-weight: 400;
        line-height: 1.2;
        white-space: nowrap;
        color: $grey;
        border-bottom: 1px solid $red;
        box-sizing: border-box;

        @media (min-width: 768px) {
            min-width: rem(193);
            font-size: rem(15);
        }
        @media (min-width: 1024px) {
            min-width: rem(228);
            font-size: rem(19);
        }

        @media (min-width: 1600px) {
            min-width: rem(265);
            font-size: rem(25);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
        }

        &-title {
            padding: 0 rem(20);
            text-align: center;
            transition: all 0.8s ease-in-out;
         
            span {
                display: none;
                @media (min-width: 768px) {
                    display: inline-block;
                }
            }
        }
        &:last-child {
            border-right: none;
        }

        &:hover {
            color: $grey_light_more_2;
        }

        &.is-active {
            padding-bottom: 1px;
            color: $grey_light_more_2;
            border: rem(2) solid $red;
            border-bottom: none;
            box-sizing: border-box;

            @media (min-width: 768px) {
                width: auto;
            }

            .s-rulers__tab-title {
                display: flex;
                justify-content: center;
                border: none;

                span {
                    display: inline-block;
                    margin-right: 5px;
                }
            }
        }
        &-content {
            display: none;
            margin-top: rem(15);
            @media (min-width: 768px) {
                margin-top: rem(30);
            }
            @media (min-width: 1024px) {
                margin-top: rem(50);
            }

            &.is-active {
                display: block;
            }
        }
    }
    .border-none {
        border: none;
        .s-rulers__tab-link {
            // border: none;
        }
        .s-rulers__tab-title {
            border: none;
        }
    }
}
